.file {
  &__input {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    left: -99999px;
  }
  &__wrapper {
    display: inline-block;
  }
  &-button {
    display: inline-flex;
    align-items: center;
    color: $gray;
    transition: $transition;
    cursor: pointer;
    &:hover {
      color: $blue-active;
      .file-button__text {
        text-decoration: none;
      }
    }
    &__icon {
      width: 20px;
      height: 18px;
      margin-right: 16px;
    }
    &__text {
      text-decoration: underline;
      font-size: 16px;
    }
  }
  &-block {
    list-style: none;
    font-size: 16px;
    color: $gray;
    &__item {
      padding-top: 10px;
      display: flex;
      align-items: center;
    }
    &__delete {
      width: 24px;
      height: 24px;
      transition: $transition;
      cursor: pointer;
      margin-left: 8px;
      &:hover {
        color: $blue-active;
      }
    }
  }
}