.main-slider {
  position: relative;
  overflow: hidden;
  &__bottom {
    position: absolute;
    bottom: 60px;
    left: 0;
    right: 0;
    z-index: 10;
  }
  &__arrow {
    position: absolute;
    bottom: 16px;
    left: 0;
    right: 0;
    z-index: 11;
    margin: 0 auto;
    width: 48px;
    height: 25px;
    cursor: pointer;
    &:hover {
      animation-name: slideDown;
      animation-duration: 1s;
      animation-timing-function: ease;
    }
  }
  @media screen and (max-width: 1000px){
    &__bottom {
      display: none;
    }
    &__arrow {
      display: none;
    }
  }
}
