.help {
  margin-left: 10px;
  position: relative;
  &-wrapper {
    display: inline-flex;
  }
  &__icon {
    width: 20px;
    min-width: 20px;
    height: 20px;
    cursor: pointer;
    transition: $transition;
  }
  &__block {
    position: absolute;
    bottom: 100%;
    margin-bottom: 15px;
    background: $white;
    filter: drop-shadow(0px 2px 8px rgba(0, 0, 0, 0.15));
    padding: 15px;
    left: 50%;
    transform: translateX(-50%);
    width: 200px;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
    z-index: -1;
    font-size: 14px;
    &::after {
      top: 100%;
      left: 50%;
      border: solid transparent;
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
      border-color: rgba(255, 255, 255, 0);
      border-top-color: $white;
      border-width: 7px;
      margin-left: -7px;
    }
    &::before {
      top: 100%;
      left: 0;
      right: 0;
      height: 15px;
      position: absolute;
      content: '';
    }
  }
  &:hover {
    .help__icon {
      color: $blue-active;
    }
    .help__block {
      opacity: 1;
      visibility: visible;
      z-index: 3;
    }
  }
  @media screen and (max-width: 768px) {
  }
}