.product_added {
  .product__image {
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      position: relative;
      z-index: 3;
    }
  }
  .product__bg {
    border-radius: 14px;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 181px;
    height: 192px;
  }
  .product__title {
    max-width: 500px;
  }
  .product__price {
    text-align: center;
    font-size: 14px;
    padding-top: 10px;
    .price {
      font-size: 14px;
    }
  }
  .product__sum {
    .price {
      font-size: 26px;
    }
  }
  .product__visual {
    width: 260px;
    min-width: 260px;
  }
  .product__button {
    display: flex;
    margin: 0 -6px;
    .button {
      margin: 0 6px;
      text-transform: none;
      min-width: 210px;
    }
  }
  .product__view:hover {
    .product__image img {
      transform: scale(1.05);
    }
  }
  &:hover {
    .product__image img {
      transform: none;
    }
  }
  @media screen and (max-width: 768px) {
    .grid-wrap {
      display: block;
      height: auto;
    }
    .product__code {
      margin-bottom: 4px;
    }
    .product__actions {
      margin-bottom: 5px;
    }
    .product__price {
      margin-bottom: 8px;
    }
    .price {
      font-size: 32px;
    }
  }
  @media screen and (max-width: 576px) {
    .product__visual {
      width: 100px;
      min-width: 100px;
    }
    .product__bg {
      width: 70px;
      height: 70px;
    }
    .product__image {
      height: 100px;
    }
    .button {
      height: 34px;
      min-width: 100%;
    }
  }
}