.label {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  min-width: 40px;
  height: 24px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 5px;
  font-family: $font-text800;
  font-size: 12px;
  color: $white;
  margin: 3px 0;
  text-transform: uppercase;
  &_discount {
    min-width: 58px;
    position: absolute;
    bottom: 0;
    left: 0;
    margin: 0;
  }
  &-block {
    padding-top: 7px;
    position: absolute;
    top: 0;
    left: 2px;
    display: flex;
    flex-direction: column;
    z-index: 5;
  }
  &_present {
    display: block;
    width: 115px;
    height: 115px;
    position: absolute;
    top: -20px;
    left: -40px;
    margin: 0;
    z-index: 10;
    box-shadow: none;
  }
  @media screen and (max-width: 768px){
    &_present {
      width: 64px;
      height: 64px;
      top: -10px;
      left: -10px;
    }
  }
}