.product_single {
  height: 100%;
  padding: 0 6px 19px;
  .favorite {
    position: absolute;
    top: 17px;
    right: 17px;
    z-index: 5;
  }
  .product__image {
    display: inline-block;
    overflow: hidden;
    width: 140px;
    height: 174px;
  }
  .product__name {
    font-size: 14px;
    margin-bottom: 0;
  }
  .product__title {
    font-size: 18px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 43px;
  }
  .button {
    min-width: 139px;
  }
  .product__button {
    position: absolute;
    bottom: 0;
    left: 31px;
    right: 31px;
  }
  .product__price {
    margin-bottom: 10px;
  }
  @media screen and (max-width: 768px) {
    padding: 0 4px 19px;
    .product__wrapper {
      display: flex;
      flex-direction: column;
    }
    .product__price {
      flex-grow: 1;
    }
    .product__title {
      font-size: 16px;
    }
    .product__button {
      left: 18px;
      right: 17px;
    }
    .product__image {
      width: 82px;
      height: 104px;
    }
    .button {
      height: 34px;
    }
  }
  @media screen and (max-width: 374px) {
    .product__button {
      left: 4px;
      right: 4px;
    }
  }
}
.present-slider {
  .product_single {
    padding-bottom: 50px;
    padding-top: 15px;
    .product__wrapper  {
      cursor: pointer;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
    }
    &.is-selected,
    &:hover {
      .product__wrapper  {
        box-shadow: 16px 12px 36px rgba(0, 0, 0, 0.1);
      }
    }
  }
  .slider_product {
    margin-bottom: 12px;
    .slick-dots {
      bottom: -12px;
    }
  }
  @media screen and (max-width: 768px) {
    .product_single {
      padding-bottom: 8px;
      padding-top: 12px;
      .product__wrapper  {
        box-shadow: 2px 2px 5px rgba(47, 36, 35, 0.05);
      }
    }
  }
}