.product-list {
  margin: 0 -6px;
  width: 100%;
  .grid-col {
    padding-bottom: 67px;
  }
  &_wish {
    .product {
      .product__wrapper {
        cursor: pointer;
        border: 1px solid $white;
      }
       &:hover {
         .product__wrapper {
          border-color: $blue-active;
        }
      }
    }

  }
  @media screen and (max-width: 768px) {
    margin: 0 -4px;
    .grid-col {
      padding-bottom: 50px;
    }
  }
}