.input {
  font-family: $font-text;
  font-size: 16px;
  display: block;
  width: 100%;
  transition: $transition;
  background: transparent;
  color: $dark;
  height: 32px;
  border: 0;
  border-bottom: 1px solid $gray;
  &:focus {
    border-color: $green;
  }
  &:-webkit-input-placeholder {
    color: $gray;
    opacity: 1;
  }
  &::-moz-placeholder {
    color: $gray;
    opacity: 1;
  }
  &:-moz-placeholder {
    color: $gray;
    opacity: 1;
  }
  &:-ms-input-placeholder {
    color: $gray;
    opacity: 1;
  }
  &.is-error {
    border-color: $red;
  }
  &_text {
    resize: none;
    height: 56px;
  }
}