.price {
  display: inline-block;
  white-space: nowrap;
  font-family: $font-text700;
  &_new {
    color: $red;
    margin-right: 0.9em;
  }
  &_old {
    position: relative;
    font-family: $font-text600;
    &::after {
      position: absolute;
      transform: rotate(-10deg);
      height: 1px;
      left: 0;
      right: 0;
      top: 50%;
      content: '';
      background: $red;
    }
  }
  &__rub {
    font-size: 0.9em;
    vertical-align: bottom;
  }
}