.filter {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 -10px 49px;
  font-size: 14px;
  color: $gray; 
  &__item {
    min-width: 0;
    padding: 0 10px;
  }
  .checkbox {
    font-size: 14px;
    &__icon {
      margin: 0 4px;
      width: 16px;
      min-width: 16px;
      height: 16px;
    }
    &__text {
      min-height: 16px;
      padding: 0 4px;
    }
    &__wrapper {
      margin: 0 -4px;
    }
  }
  &__link {
    color: $blue-active;
    cursor: pointer;
    transition: $transition;
    &:hover {
      color: $dark;
    }
  }
  @media screen and (max-width: 768px) {
    margin: 0 -15px 16px;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.05);
    &__item {
      padding: 0 15px 16px;
    }
    &__open {
      cursor: pointer;
      transition: $transition;
      font-size: 14px;
      color: $graffit;
      &.is-active,
      &:hover {
        font-family: $font-text500;
        color: $dark;
      }
    }
    &-link {
      display: flex;
      align-items: center;
      margin: 0 -8px;
      cursor: pointer;
      transition: $transition;
      &__arrow  {
        display: block;
        width: 6px;
        min-width: 6px;
        height: 12px;
        margin: 0 8px;
      }
      &__text {
        padding: 0 8px;
      }
      &:hover {
        color: $blue-active;
      }
    }
    &__clear {
      cursor: pointer;
      transition: $transition;
      color: $gray;
      &:hover {
        color: $blue-active;
      }
    }

  }
}