.rate {
  display: flex;
  align-items: center;
  margin: 0 -3px;
  flex-direction: row-reverse;
  &-wrapper {
    display: flex;
    align-items: center;
  }
  &__title {
    margin-right: 30px;
    color: $dark;
  }
  &__item {
    margin: 0 3px;
    cursor: pointer;
    transition: $transition;
    color: $light-gray;
    &:hover,
    &:hover ~ .rate__item,
    &.is-selected,
    &.is-selected ~ .rate__item {
      color: $blue-light;
    }
  }
  &__icon {
    width: 20px;
    min-width: 20px;
    height: 20px;
  }
}