.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 14px;
  &__item {
    padding: 0 5px;
  }
  &__link {
    font-size: 14px;
    color: $gray;
    &.is-current {
      color: $dark;
      text-decoration: underline;
    }
    &:hover {
      color: $blue-light;
    }
  }
}