.set {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 640px;
  margin-bottom: 0;
  padding: 0;
  position: relative;
  &__item {
    padding: 0 10px;
    &:first-child {
      display: flex;
      align-items: center;
      height: 100%;
    }
  }
  &__button {
    position: absolute;
    right: 0;
    bottom: 0;
  }
  @media screen and (max-width: 1000px) {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    height: auto;
    margin-bottom: 0;
    padding: 0;
    justify-content: center;
    align-items: center;
    &__item {
      &:first-child {
        display: block;
      }
    }
    &__button {
      display: none;
    }
  }
  @media screen and (max-width: 768px){
    &__item {
      padding: 0;
      &:nth-child(2) {
        order: 1;
      }
    }
  }
}