.menu-aside {
  list-style: none;
  font-size: 16px;
  line-height: 1.75;
  position: sticky;
  top: 135px;
  &__item {
    margin-bottom: 8px;
  }
  &__link {
    color: $dark;
    font-family: $font-text500;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover,
    &.is-active {
      color: $black;
      font-family: $font-text700;
    }
    &.is-active {
      .menu-aside__arrow {
        transform: rotate(-90deg);
      }
    }
    &_special {
      color: $blue-active;
      &:hover,
      &.is-active {
        color: $blue-active;
      }
    }
  }
  &__text {
    padding-right: 15px;
  }
  &__arrow {
    display: block;
    width: 6px;
    min-width: 6px;
    height: 12px;
    transition: $transition;
    transform: rotate(90deg);
    margin-right: 6px;
  }
  &__sub {
    display: none;
  }
  @media screen and (min-width: 1001px) {
    &__sub {
      &.is-open {
        display: block;
      }
    }
  }
  @media screen and (max-width: 1000px) {
    margin: 0 -15px;
    display: flex;
    overflow-x: auto;
    padding-bottom: 8px;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.05);
    &__item {
      white-space: nowrap;
      padding: 0 15px;
    }
  }
}