.slide-card {
  display: block;
  &__title {
    font-family: $font-text700;
    font-size: 48px;
    line-height: 1.08;
    margin-bottom: 20px;
  }
  &__text {
    font-size: 24px;
    color: $graffit;
    margin-bottom: 60px;
  }
  &_full {
    .slide-card__title {
      margin-bottom: 54px;
    }
    .grid-wrap {
      min-height: 410px;
    }
  }
  @media screen and (max-width: 1000px){
    &_full {
      .slide-card__title {
        margin-bottom: 30px;
      }
      .grid-wrap {
        min-height: 0;
        display: block;
      }
      .slide-card__button {
        text-align: center;
      }
    }
  }

  @media screen and (max-width: 768px){
    &__title {
      margin-bottom: 20px;
    }
    &:not(.slide-card_full) {
      .slide-card__text {
        color: $white;
      }
    }
    &_full {
      .slide-card__title {
        margin-bottom: 8px;
        font-size: 20px;
      }
      .slide-card__text {
        font-size: 16px;
        margin-bottom: 22px;
      }
    }
  }
}
