.form {
  label.error {
    color: $red;
    font-size: 12px;
    padding-top: 5px;
  }
  &__top {
    margin-bottom: 80px;
  }
  &-group {
    margin-bottom: 60px;
    max-width: 545px;
    &_radio {
      max-width: none;
      .form__note {
        padding-top: 19px;
        display: block;
        max-width: 265px;
      }
    }
    &__title {
      font-family: $font-text600;
      font-size: 24px;
      color: $dark;
      margin-bottom: 30px;
    }
  }
  &__item {
    margin-bottom: 30px;
    &_narrow {
      margin-bottom: 20px;
    }
  }
  &__label {
    font-size: 16px;
    line-height: 1.5;
    color: $gray;
    display: block;
    margin-bottom: 6px;
  }
  &__wrapper {
    position: relative;
    &.is-complete {
      .form__icon {
        opacity: 1;
      }
    }
  }
  &__icon {
    width: 15px;
    height: 11px;
    color: $blue-active;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    transition: $transition;
    opacity: 0;
  }
  &__note {
    font-family: $font-text500;
    font-size: 12px;
    color: $gray;
    padding-top: 6px;
  }
  &__button {
    margin-bottom: 22px;
  }
  &-agree {
    max-width: 800px;
    font-family: $font-text300;
    font-size: 14px;
    color: $gray;
    line-height: 1.71;
    padding-bottom: 60px;
    &__link {
      color: $dark;
      &:hover {
        color: $blue-active;
      }
    }
  }
  @media screen and (max-width: 768px){
    &__top {
      margin-bottom: 20px;
    }
    &-group {
      margin: 0 -15px 15px;
      max-width: none;
      padding: 25px 15px 5px;
      background: $white;
      &__title {
        font-size: 20px;
        margin-bottom: 14px;
      }
      &_radio {
        .grid-col {
          padding-bottom: 20px;
        }
        .form__note {
          padding-top: 5px;
          width: 100%;
        }
      }
    }
    &__item {
      margin-bottom: 20px;
    }
    &-agree {
      padding-bottom: 0;
    }
    &__button {
      padding-top: 20px;
      text-align: center;
    }
  }
  @media screen and (max-width: 576px) {
    &__button {
      .button {
        width: 100%;
      }
    }
  }
}