.advantages {
  display: flex;
  justify-content: space-around;
  &__item {
    padding: 0 15px;
  }
  &__block {
    max-width: 200px;
    display: flex;
    align-items: center;
  }
  &__icon {
    width: 34px;
    min-width: 34px;
    margin-right: 12px;
    img {
      display: block;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 1.29;
  }
  @media screen and (max-width: 768px) {
    display: none;
  }
}