.favorite {
  display: flex;
  align-items: center;
  color: $dark;
  font-family: $font-text600;
  font-size: 16px;
  cursor: pointer;
  transition: none;
  &__icon {
    width: 26px;
    min-width: 26px;
    height: 23px;
    transition: $transition;
  }
  &__text {
    margin-left: 10px;
    transition: $transition;
  }
  &.is-selected {
    color: $blue-light;
  }
  @media screen and (max-width: 768px) {
    &__icon {
      width: 19px;
      min-width: 19px;
      height: 17px;
    }
  }
}