.special {
  padding: 77px 0 70px;
  position: relative;
  &__name {
    font-family: $font-text600;
    font-size: 32px;
    color: $gray;
    margin-bottom: 24px;
  }
  &__title {
    font-family: $font-text600;
    font-size: 48px;
  }
  &__number {
    font-family: $font-text700;
    font-size: 124px;
    line-height: 1;
    &_big {
      font-family: $font-text500;
      font-size: 164px;
    }
  }
  &__bg {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    bottom: 0;
    background-color: $light-pink;
    &_full {
      width: 100%;
    }
    &_left {
      width: 55%;
    }
    &_right {
      width: 45%;
      left: auto;
      right: 0;
    }
  }
  &__note {
    font-family: $font-text600;
    font-size: 24px;
    color: $gray;
    margin-top: 40px;
  }
  &__date {
    font-family: $font-text600;
    font-size: 76px;
    line-height: 1.11;
    color: $graffit;
  }
  @media screen and (max-width: 1280px) {
    &__number {
      text-align: center;
    }
  }
  @media screen and (max-width: 768px) {
    padding: 41px 0 40px;
    background-color: $light-pink;
    &__name {
      font-size: 18px;
      margin-bottom: 14px;
    }
    &__title {
      font-size: 24px;
    }
    &__number {
      font-size: 88px;
      &_big {
        font-size: 88px;
      }
    }
    &__bg {
      display: none;
    }
    &__date {
      font-size: 56px;
    }
    &__note {
      font-size: 14px;
      margin-top: 8px;
    }
  }
}
