.select {
  margin: 0 -12px;
  .select2-container--default .select2-selection--single {
    background-color: transparent;
    border: none;
    border-radius: 0;
    height: 40px;
  }
  .select2-container--default .select2-selection--single .select2-selection__rendered {
    color: $dark;
    line-height: 40px;
    padding: 0 40px 0 12px;
  }
  .select2-container--default .select2-selection--single .select2-selection__arrow {
    width: 12px;
    height: 6px;
    transition: $transition;
    background: url(icons/select-arrow.svg);
    right: 12px;
    top: 50%;
    margin-top: -3px;
  }
  .select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow {
    transform: rotate(180deg);
  }
  .select2-container--default .select2-selection--single .select2-selection__arrow b {
    display: none;
  }
  .select2-container {
    transition: $transition;
  }
  .select2-container--open {
    background: $white;
  }
  .select2-container--below {
    border-radius: 14px 14px 0 0;
  }
  .select2-container--above {
    border-radius: 0 0 14px 14px;
  }
  .select2-container--default .select2-selection--single .select2-selection__placeholder {
    color: $gray;
  }
  .select2-container--default .select2-selection--multiple {
    background-color: transparent;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    padding: 0 30px 0 12px;
  }
  .select2-search.select2-search--inline {
    max-width: 53px;
    display: block;
  }
  .select2-container .select2-search--inline .select2-search__field {
    margin: 0;
    color: $gray;
    font-family: $font-text;
    height: 40px;
    &:-webkit-input-placeholder {
      color: $gray;
      opacity: 1;
    }
    &::-moz-placeholder {
      color: $gray;
      opacity: 1;
    }
    &:-moz-placeholder {
      color: $gray;
      opacity: 1;
    }
    &:-ms-input-placeholder {
      color: $gray;
      opacity: 1;
    }
  }
  .select2-selection--multiple:before {
      content: "";
      position: absolute;
      width: 12px;
      height: 6px;
      transition: $transition;
      background: url(icons/select-arrow.svg);
      right: 12px;
      top: 50%;
      margin-top: -3px;
  }
  .select2-container--open .select2-selection--multiple:before {
    transform: rotate(180deg);
  }
  .select2-container--default .select2-selection--multiple .select2-selection__choice {
    background-color: transparent;
    border: 1px solid $grey-border;
    margin-top: 0;
    line-height: 30px;
    color: $dark;
    padding: 0 5px 0 0;
  }
  .select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    border-right: none;
    transition: $transition;
  }
  .select2-container--default .select2-selection--multiple .select2-selection__choice__remove:hover {
    color: $blue-active;
    background-color: transparent;
  }
}

.select-image {
  .select2-container {
    min-width: 216px;
  }
  .select2-container--default .select2-selection--single {
    background-color: $pink;
    border-color: $light-gray;
    transition: $transition;
  }
  .select2-container--default.select2-container--open .select2-selection--single {
    border-color: $dark;
  }
  .select2-container .select2-selection--single {
    height: 42px;
  }
  .select2-container--default .select2-selection--single .select2-selection__rendered {
    font-family: $font-text500;
    font-size: 16px;
    letter-spacing: 0.02em;
    color: $graffit;
    line-height: 42px;
  }
  .select2-container--default .select2-selection--single .select2-selection__arrow {
    width: 12px;
    height: 6px;
    transition: $transition;
    background: url(icons/select-arrow.svg);
    right: 18px;
    top: 50%;
    margin-top: -3px;
  }
  .select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow {
    transform: rotate(180deg);
  }
  .select2-container--default .select2-selection--single .select2-selection__arrow b {
    display: none;
  }
  &__icon {
    vertical-align: middle;
  }
  &__text {
    padding-left: 11px;
  }
}

.select-image__dropdown.select2-dropdown {
  background-color: $pink;
  border-color: $dark;
}
.select2-container--open .select-image__dropdown.select2-dropdown--above {
  border-bottom: 1px solid;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  margin-bottom: 8px;
}
.select2-container--open .select-image__dropdown.select2-dropdown--below {
  border-top: 1px solid;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  margin-top: 8px;
}
.select2-container--default .select-image__dropdown .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: transparent;
  color: $blue-active;
}
.select2-container--default .select-image__dropdown .select2-results__option--selected {
  background-color: transparent;
}
.select-image__dropdown .select2-results__option {
  padding: 12px 9px;
  font-family: $font-text500;
  font-size: 16px;
  letter-spacing: 0.02em;
  color: $graffit;
  transition: $transition;
}

.select__dropdown.select2-dropdown {
  background-color: $white;
  border: none;
  overflow: hidden;
  padding-bottom: 6px;
}
.select2-container--open .select__dropdown.select2-dropdown--below {
  box-shadow: 0px 20px 20px rgba(160, 160, 160, 0.1);
  border-radius: 0 0 14px 14px;
  margin-bottom: 8px;
}
.select2-container--open .select__dropdown.select2-dropdown--above {
  box-shadow: 0px -20px 20px rgba(160, 160, 160, 0.1);
  border-radius: 14px 14px 0 0;
  margin-top: 8px;
}
.select2-container--default .select__dropdown .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: transparent;
  color: $blue-active;
}
.select2-container--default .select__dropdown .select2-results__option--selected {
  background-color: transparent;
}
.select__dropdown .select2-results__option {
  padding: 6px 12px;
  font-family: $font-text500;
  font-size: 14px;
  letter-spacing: 0.02em;
  color: $graffit;
  transition: $transition;
}