.catalog {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  z-index: -1;
  background: rgba(0, 0, 0, 0.35);
  transform: translateY(-100%);
  transition: $transitionSlow;
  visibility: hidden;
  &__wrapper {
    background: $pink;
    padding: 50px 0;
    height: 100%;
    max-height: 635px;
  }
  &.is-open {
    transform: translateY(0);
    z-index: 200;
    visibility: visible;
  }
  .content {
    height: 100%;
  }
  .grid-row {
    height: 100%;
  }
  &__close {
    display: inline-block;
    width: 23px;
    height: 26px;
    cursor: pointer;
    transition: $transition;
    color: $dark;
    &:hover {
      color: $blue-active;
    }
  }
  &-aside {
    padding-left: 40px;
    border-left: 1px solid $grey-border;
    height: 100%;
    color: $dark;
    &__bottom {
      padding-top: 21px;
      border-top: 1px solid $grey-border;
    }
    &__content {
      height: 100%;
      overflow-y: auto;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &__top {
      text-align: right;
      width: 100%;
      padding-bottom: 20px;
    }
    &__logo {
      padding-bottom: 20px;
      width: 100%;
      .logo {
        width: 205px;
      }
    }
    &__contacts {
      padding-bottom: 59px;
      width: 100%;
    }
  }
  &__title {
    font-family: $font-text600;
    color: $grey;
    text-transform: uppercase;
    margin-bottom: 8px;
  }
  &__menu {
    position: relative;
  }
  &__col {
    height: 100%;
    overflow-y: auto;
    transition: $transition;
    &.is-active {
      z-index: 1;
      visibility: visible;
      opacity: 1;
    }
    &_first {
      width: 216px;
    }
    &_second {
      width: 248px;
      padding-left: 32px;
      position: absolute;
      top: 0;
      left: 216px;
      opacity: 0;
      visibility: visible;
      z-index: -1;
    }
    &_third {
      width: auto;
      right: 0;
      padding-left: 32px;
      position: absolute;
      top: 0;
      left: 464px;
      opacity: 0;
      visibility: visible;
      z-index: -1;
    }
    &_full {
      width: auto;
      right: 0;
    }
  }
  &-back {
    text-align: center;
    margin-bottom: 16px;
    &__link {
      display: inline-flex;
      align-items: center;
      cursor: pointer;
      font-family: $font-text600;
      color: $dark;
      transition: $transition;
      &:hover {
        color: $blue-active;
      }
    }
    &__arrow {
      display: block;
      width: 6px;
      min-width: 6px;
      height: 12px;
      transform: rotate(180deg);
      margin-right: 25px;
    }
  }
  &__banner {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    img {
      max-height: 100%;
      width: auto;
      display: block;
    }
  }
  @media screen and (max-width: 1280px) {
    &-aside {
      padding-left: 40px;
      &__logo {
        .logo {
          width: 140px;
        }
      }
    }
  }

  @media screen and (max-width: 1000px) {
    &-aside {
      border-left: none;
      border-top: 1px solid #B8B8B8;
      padding: 17px 0;
      height: auto;
      &__top {
        position: absolute;
        bottom: 100%;
        left: 16px;
        text-align: left;
        width: auto;
        z-index: 100;
      }
      &__content {
        height: auto;
        overflow-y: visible;
        display: block;
      }
      &__contacts {
        padding-bottom: 17px;
      }
    }
    &__wrapper {
      max-height: 100%;
      padding: 60px 0 20px;
      overflow-y: auto;
    }
    .content {
      position: relative;
    }
    .grid-col_75 {
      flex: 1 0 auto;
    }
    &__menu {
      margin-bottom: 30px;
    }
  }
  @media screen and (max-width: 768px) {
    &__col {
      width: 100%;
      &.is-active {
        z-index: 20;
      }
      &_second {
        padding: 0;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: $pink;
      }
      &_third {
        padding: 0;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: $pink;
      }
      &_full {
        width: auto;
        right: 0;
      }
    }
    &-aside {
      .grid-wrap {
        flex-direction: row;
        align-items: center;
      }
      .contacts__item:not(:last-child) {
        margin-bottom: 0;
      }
      &__contacts {
        padding: 0;
      }
      &__bottom {
        padding-top: 0;
        border-top: none;
      }
    }
    &__banner {
      justify-content: center;
      height: calc(100% - 40px);
    }
  }
}