.soc {
  display: flex;
  margin: 0 -8px;
  align-items: center;
  &__item {
    padding: 0 8px;
  }
  &__link {
    display: block;
    min-width: 24px;
    min-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      color: $blue-active;
    }
  }
  &__icon {
    display: block;
    &_facebook {
      width: 20px;
      min-width: 20px;
      height: 20px;
    }
    &_instagram {
      width: 20px;
      min-width: 20px;
      height: 20px;
    }
    &_youtube {
      width: 20px;
      min-width: 20px;
      height: 16px;
    }
    &_whatsapp {
      width: 24px;
      min-width: 24px;
      height: 24px;
    }
    &_telegram {
      width: 28px;
      min-width: 28px;
      height: 24px;
    }
  }
  &_wide {
    margin: 0 -15px;
    .soc__item {
      padding: 0 15px;
    }
  }
  @media (max-width: 767px) {

  }
}
