.checkbox {
  display: inline-block;
  cursor: pointer;
  font-size: 20px;
  input[type="checkbox"] {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    left: -99999px;
    &:checked + .checkbox__wrapper {
      color: $dark;
      .checkbox__icon {
        border: 1px solid $dark;
        .svg-icon {
          opacity: 1;
        }
      }
    }
  }
  &__wrapper {
    display: flex;
    min-width: 20px;
    margin: 0 -8px;
  }
  &__text {
    padding: 0 8px;
    display: flex;
    min-height: 20px;
    align-items: center;
  }
  &__icon {
    width: 20px;
    min-width: 20px;
    height: 20px;
    transition: $transition;
    border: 1px solid $gray;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 8px;
    .svg-icon {
      opacity: 0;
      transition: $transition;
      width: 18px;
      height: 14px;
      color: $dark;
    }
  }
  @media screen and (max-width: 768px) {
    font-size: 16px;
    &__wrapper {
      min-width: 16px;
      margin: 0 -6px;
    }
    &__text {
      padding: 0 6px;
      min-height: 16px;
    }
    &__icon {
      width: 16px;
      min-width: 16px;
      height: 16px;
      margin: 0 6px;
      .svg-icon {
        width: 15px;
        height: 11px;
      }
    }
  }
}