.dummy {
  height: 100%;
  padding: 0 6px 19px;
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px;
    background: linear-gradient(89.04deg, #FFFFFF 16.26%, #FFF6F7 104.18%);
    border-radius: 14px;
    height: 100%;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.05);
    color: $grey;
  }
  &__title {
    font-size: 34px;
    font-family: $font-text600;
    opacity: 0.3;
  }
  &__text {
    font-weight: 500;
    font-size: 30px;
    font-family: $font-text500;
    opacity: 0.3;
  }
  @media screen and (max-width: 768px) {
    &__title {
      font-size: 24px;
    }
    &__text {
      font-size: 20px;
    }
  }
}
