.burger {
  width: 29px;
  height: 21px;
  position: relative;
  cursor: pointer;
  &__line {
    position: absolute;
    height: 3px;
    transition: 0.3s ease;
    background: $dark;
    right: 0;
    &:nth-child(1) {
      width: 21px;
      top: 0;
    }
    &:nth-child(2) {
      top: 50%;
      transform: translateY(-50%);
      width: 25px;
    }
    &:nth-child(3) {
      bottom: 0;
      width: 29px;
    }
  }
  &:hover {
    .burger__line {
      background: $blue-active;
      width: 29px;
    }
  }
}