.product_review {
  .product__image {
    height: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      position: relative;
      z-index: 3;
    }
  }
  .product__bg {
    border-radius: 14px;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 111px;
    height: 118px;
  }
  .product__visual {
    width: 160px;
    min-width: 160px;
  }
  .product__name {
    margin-bottom: 0;
    display: inline;
    font-size: 18px;
  }
  .product__title {
    margin-bottom: 0;
    display: inline;
    font-size: 18px;
  }
  .product__view:hover {
    .product__image img {
      transform: scale(1.05);
    }
  }
  &:hover {
    .product__image img {
      transform: none;
    }
  }
  .grid-col_full .grid-row {
    height: 100%;
  }
  @media screen and (max-width: 768px) {
    .product__visual {
      width: 130px;
      min-width: 130px;
    }
    .product__bg {
      width: 92px;
      height: 91px;
    }
    .product__image {
      height: 127px;
    }
  }
  @media screen and (max-width: 576px) {
    .product__visual {
      width: 100px;
      min-width: 100px;
    }
    .product__bg {
      width: 80px;
      height: 81px;
    }
    .product__image {
      height: 100px;
    }
  }
}