h1 {
  font-size: 26px;
  font-family: $font-text500;
  line-height: 1;
  font-weight: 400;
}
h2 {
  font-size: 48px;
  font-family: $font-text700;
  line-height: 1;
  font-weight: 400;
}
h3 {
  font-size: 26px;
  font-family: $font-text500;
  line-height: 1;
  font-weight: 400;
}
h4 {
  font-size: 30px;
  font-family: $font-text600;
  line-height: 1;
  font-weight: 400;
}
h5 {
  font-size: 16px;
  font-family: $font-text;
  text-transform: uppercase;
  font-weight: 400;
}
p {
  margin-bottom: 16px;
  color: $gray;
}
@media screen and (max-width: 768px) {
  h2 {
    font-size: 24px;
  }
  h1 {
    font-size: 18px;
  }
  h4 {
    font-size: 24px;
  }
}
