.contacts {
  &__item {
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }
  &__link {
    &:hover {
      color: $blue-active;
    }
  }
  &__group {
    display: inline-flex;
    align-items: center;
  }
  &__icon {
    width: 19px;
    min-width: 19px;
    height: 19px;
    margin-right: 16px;
  }
  p {
    margin-bottom: 0;
  }
  &-menu {
    list-style: none;
    color: $dark;
    display: flex;
    align-items: center;
    margin: 0 -30px;
    &__item {
      padding: 0 30px;
    }
    &__link {
      &:hover {
        color: $blue-active;
      }
    }
  }
  &_part {
    margin-bottom: 60px;
  }
  &__form {
    h3 {
      margin-bottom: 12px;
    }
  }
  &__note {
    color: $gray;
    margin-bottom: 20px;
  }
  @media screen and (max-width: 768px) {
    &_part {
      margin-bottom: 40px;
    }
  }
}