.popup {
  .mfp-counter {
    display: none;
  }
  &.mfp-bg {
    background: $black;
    opacity: 0.5;
    transition: $transitionSlow;
  }
  .mfp-content {
    transition: $transitionSlow;
  }
  &_down {
    &.mfp-bg {
      transform: translateY(-100%);
      &.mfp-ready {
        transform: translateY(0);
      }
      &.mfp-removing {
        transform: translateY(-100%);
        transition-delay: 0.5s;
      }
    }
    &.mfp-wrap {
      .mfp-content {
        transform: translateY(-150%);
      }
      &.mfp-ready {
        .mfp-content {
          transform: translateY(0);
          transition-delay: 0.3s;
        }
      }
      &.mfp-removing {
        .mfp-content {
          transform: translateY(-150%);
        }
      }
    }
  }

  &_left {
    &.mfp-bg {
      transform: translateX(100%);
      &.mfp-ready {
        transform: translateX(0);
      }
      &.mfp-removing {
        transform: translateX(100%);
        transition-delay: 0.3s;
      }
    }
    &.mfp-wrap {
      .mfp-container {
        padding: 0;
        display: flex;
        justify-content: flex-end;
      }
      .mfp-content {
        transform: translateX(100%);
        display: flex;
        justify-content: flex-end;
        height: 100%;
        margin: 0;
        .mfp-close {
          display: none;
        }
      }
      &.mfp-ready {
        .mfp-content {
          transform: translateX(0);
        }
      }
      &.mfp-removing {
        .mfp-content {
          transform: translateX(100%);
        }
      }
    }
  }

  &_up {
    &.mfp-bg {
      transform: translateY(100%);
      &.mfp-ready {
        transform: translateY(0);
      }
      &.mfp-removing {
        transform: translateY(100%);
        transition-delay: 0.5s;
      }
    }
    &.mfp-wrap {
      .mfp-content {
        transform: translateY(150%);
        .mfp-close {
          display: none;
        }
      }
      &.mfp-ready {
        .mfp-content {
          transform: translateY(0);
          transition-delay: 0.3s;
        }
      }
      &.mfp-removing {
        .mfp-content {
          transform: translateY(150%);
        }
      }
    }
  }
  &-block {
    height: 100vh;
    background: $pink;
    color: $dark;
    width: 100%;
    padding: 32px 20px 16px 25px;
    overflow-y: auto;
    position: relative;
    &_large {
      max-width: 520px;
    }
    &__wrapper {
      display: flex;
      flex-direction: column;
      height: 100%;
      &_middle {
        justify-content: center;
      }
    }
    &__top {
      display: flex;
      width: 100%;
      justify-content: space-between;
      font-family: $font-text600;
      color: $dark;
      font-size: 14px;
      margin-bottom: 46px;
    }
    &__content {
      flex: 1 0 auto;
      min-height: 0;
      font-family: $font-text500;
    }
    &__item {
      margin-bottom: 19px;
    }
    &__bottom {
      padding: 16px 0;
      .button {
        width: 100%;
      }
    }
    &__title {
      text-align: center;
      padding: 25px 0;
      font-size: 20px;
      font-family: $font-text600;
      color: $dark;
    }
    h2 {
      margin-bottom: 64px;
    }
  }
  &__close {
    width: 23px;
    height: 26px;
    cursor: pointer;
    transition: 0.3s ease-out;
    color: $dark;
    position: absolute;
    top: 16px;
    left: 16px;
    z-index: 50;
    &_right {
      left: auto;
      right: 16px;
      width: 24px;
      height: 24px;
    }
    &:hover {
      color: $blue-light;
    }
  }

  &-layout {
    max-width: 1245px;
    padding: 100px 9px 0;
    margin: auto;
    &__block {
      background: $white;
      position: relative;
      border-radius: 14px;
      padding: 30px 45px;
      &:not(:last-child) {
        margin-bottom: 25px;
      }
    }
    &__title {
      font-family: $font-text600;
      font-size: 30px;
      color: $dark;
      margin-bottom: 40px;
    }
    &__top {
      padding-left: 100px;
      margin-bottom: 30px;
    }
    &_medium {
      max-width: 858px;
    }
  }
  &-form {
    color: $dark;
    max-width: 640px;
    margin: auto;
    padding-top: 20px;
    &__title {
      font-size: 32px;
      margin-bottom: 27px;
      font-family: $font-text600;
    }
    &__text {
      margin-bottom: 60px;
    }
  }
  @media screen and (max-width: 768px){
    &-block {
      padding: 32px 15px 16px 15px;
    }
    &-layout {
      &__block {
        padding: 25px 15px;
        &:not(:last-child) {
          margin-bottom: 16px;
        }
      }
      &__top {
        padding: 0 50px;
        margin-bottom: 0;
        text-align: center;
      }
    }
  }
  @media screen and (max-width: 576px){
    &-layout {
      &__title {
        font-size: 18px;
        margin-bottom: 20px;
      }
    }
    &-form {
      &__title {
        font-size: 24px;
        margin-bottom: 20px;
      }
      &__text {
        margin-bottom: 20px;
      }
    }
  }
}