.dropdown {
  position: relative;
  margin: 0 -13px;
  display: inline-block;
  &__top {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 13px;
    height: 40px;
    transition: $transition;
  }
  &__arrow {
    display: block;
    width: 6px;
    min-width: 6px;
    height: 12px;
    transition: $transition;
    transform: rotate(90deg);
    margin-right: 6px;
  }
  &__name {
    padding-right: 12px;
  }
  &__block {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    box-shadow: 0px 20px 20px rgba(160, 160, 160, 0.1);
    border-radius: 0 0 14px 14px;
    overflow: hidden;
    background: $white;
    z-index: 3;
    display: none;
  }
  &-list {
    max-height: 200px;
    overflow-y: auto;
    padding: 0 12px 6px;
    &__item {
      cursor: pointer;
      padding: 6px 0;
      max-width: 100%;
      overflow: hidden;
    }
  }
  &__counter {
    opacity: 0;
    transition: $transition;
  }
  &.is-open {
    .dropdown__top {
      border-radius: 14px 14px 0 0;
      background: $white;
    }
    .dropdown__arrow {
      transform: rotate(-90deg);
    }
  }
  &.is-selected {
    .dropdown__name {
      color: $dark
    }
    .dropdown__counter {
      opacity: 1;
    }
  }
}
