.subcribe {
  padding: 50px 0 38px;
  background: $light-pink;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  position: fixed;
  position: sticky;
  z-index: 20;
  bottom: 0;
  left: 0;
  right: 0;
  &__block {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
  }
  &__title {
    font-family: $font-text600;
    font-size: 40px;
    line-height: 1.3;
    margin-bottom: 17px;
    font-weight: 400;
  }
  &__conditions {
    font-family: $font-text600;
    color: $gray;
  }
  &-agree {
    padding-top: 17px;
    display: flex;
    &__text {
      font-family: $font-text600;
      font-size: 16px;
      color: #949494;
      padding-left: 10px;
      line-height: 1.44;
    }
  }
  &__link {
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
  .button {
    text-transform: none;
    min-width: 161px;
  }
  &__close {
    width: 23px;
    height: 26px;
    cursor: pointer;
    transition: 0.3s ease-out;
    color: $dark;
    position: absolute;
    top: 15px;
    right: 15px;
    &:hover {
      color: $blue-light;
    }
  }
  &_static {
    position: relative;
    padding: 38px 0 38px 48px;
    margin-left: -48px;
    box-shadow: none;
    &::after {
      background: $light-pink;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 100%;
      width: 50vw;
      content: '';
    }
  }
  @media screen and (max-width: 1280px) {
    &__title {
      font-size: 30px;
    }
  }
  @media screen and (max-width: 1000px) {
    padding: 40px 0 20px;
    &__title {
      margin-bottom: 10px;
    }
    &_static {
      padding: 38px 15px 38px;
      margin: 0 -15px;
      &::after {
        display: none;
      }
    }
  }
  @media screen and (max-width: 768px) {
    &-agree {
      padding-top: 10px;
      &__text {
        font-size: 14px;
      }
    }
    .button {
      margin-top: 10px;
    }
    &__title {
      font-size: 24px;
    }
  }
  @media screen and (max-width: 576px) {
    .button {
      width: 100%;
    }
  }
}