.product_basket {
  .product__image {
    height: 187px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      position: relative;
      z-index: 3;
    }
  }
  .product__bg {
    border-radius: 14px;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 121px;
    height: 129px;
  }
  .product__visual {
    width: 160px;
    min-width: 160px;
  }
  .product__code {
    font-size: 12px;
    margin-bottom: 0;
  }
  .product__name {
    margin-bottom: 0;
    max-width: 350px;
  }
  .product__title {
    margin-bottom: 0;
    max-width: 350px;
  }
  .product__view:hover {
    .product__image img {
      transform: scale(1.05);
    }
  }
  &:hover {
    .product__image img {
      transform: none;
    }
  }
  @media screen and (max-width: 1000px) and (min-width: 769px) {
    .product__title {
      margin-bottom: 20px;
    }
  }
  @media screen and (max-width: 768px) {
    .product__visual {
      width: 130px;
      min-width: 130px;
    }
    .product__bg {
      width: 92px;
      height: 91px;
    }
    .product__image {
      height: 127px;
    }
  }
  @media screen and (max-width: 576px) {
    .product__title {
      margin-bottom: 10px;
      font-size: 16px;
    }
  }
}