.review {
  display: inline-flex;
  vertical-align: middle;
  margin: 0 -3px;
  align-items: center;
  color: $blue-light;
  font-family: $font-text600;
  font-size: 16px;
  &__item {
    display: block;
    padding: 0 3px;
  }
  img {
    display: block;
    width: 20px;
    min-width: 20px;
  }
  &:hover {
    color: $gray;
  }
  @media screen and (max-width: 768px) {
    img {
      width: 15px;
      min-width: 15px;
    }
    &_short {
      margin: 0 -1px;
      .review__link {
        display: none;
      }
      .review__item {
        padding: 0 1px;
      }
      img {
        width: 11px;
        min-width: 11px;
      }
    }
  }
}
.reviews-product {
  background: $white;
  border-radius: 14px;
  padding: 0 20px;
  margin-bottom: 50px;
  &__item {
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 30px 0;
    &_full {
      justify-content: space-between;
    }
    &_bottom {
      justify-content: flex-end;
    }
  }
  &__line {
    &:not(:last-child) {
      padding-bottom: 10px;
    }
  }
  &__status {
    color: $blue-active;
  }
  &__link {
    color: $gray;
    &:hover {
      color: $blue-active;
    }
  }
  &__rating {
    text-align: right;
  }
  .button {
    margin-right: 20px;
  }
  @media screen and (max-width: 768px) {
    padding: 15px;
    margin-bottom: 40px;
    &__item {
      height: auto;
      display: block;
      padding: 0;
    }
    &__line {
      &:last-child {
        padding-bottom: 10px;
      }
    }
    &__rating {
      text-align: left;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: row;
    }
    .button {
      margin-right: 10px;
    }
  }
  @media screen and (max-width: 576px) {
    &__rating {
      display: block;
    }
    .button {
      margin: 0 0 10px;
      width: 100%;
    }
  }
}

.review-list {
  &__item {
    margin-bottom: 40px;
  }
  &__block {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &_right {
      font-size: 14px;
      color: $gray;
      text-align: right;
    }
  }
  &__line {
    &:not(:last-child) {
      padding-bottom: 16px;
    }
  }
  &__name {
    display: inline-block;
    font-family: $font-text500;
    min-width: 176px;
    padding-right: 15px;
    vertical-align: middle;
  }
  &__text {
    font-size: 14px;
    color: $gray;
  }
  &-vote {
    display: inline-flex;
    margin: 0 -10px;
    &__item {
      padding: 0 10px;
    }
    &__link {
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }
  @media screen and (max-width: 768px) {
    &__block {
      height: auto;
      display: block;
      &_right {
        text-align: left;
      }
    }
    &__text {
      padding-bottom: 16px;
    }
  }
  @media screen and (max-width: 576px) {
    &__name {
      min-width: 0;
    }
  }
}

.reviews-form {
  border-top: 1px solid $light-gray;
  padding: 30px 0 40px;
  &__title {
    font-family: $font-text600;
    font-size: 22px;
    line-height: 28px;
    text-align: center;
    color: $dark;
    margin-bottom: 28px;
  }
  &__date {
    font-size: 14px;
    color: $gray;
    text-align: right;
  }
  @media screen and (max-width: 576px) {
    .button {
      width: 100%;
    }
  }
}