.slider {
  margin-bottom: 68px;
  padding: 0 29px;
  &__arrow {
    width: 12px;
    height: 24px;
    position: absolute;
    top: 50%;
    margin-top: -12px;
    transition: $transition;
    color: $dark;
    z-index: 2;
    cursor: pointer;
    &_next {
      right: 0;
    }
    &_prev {
      transform: rotate(180deg);
      left: 0;
    }
    &:hover {
      color: $blue-active;
    }
  }
  .slick-dots {
    position: absolute;
    bottom: -68px;
    right: 0;
    left: 0;
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      text-align: center;
      padding: 0 4px;
      &.slick-active {
        button {
          background: $graffit;
          width: 12px;
          height: 12px;
        }
      }
    }
    button {
      color: transparent;
      cursor: pointer;
      background: $dark-corall;
      font-family: $font-text;
      border: none;
      transition: $transition;
      font-size: 0;
      width: 8px;
      height: 8px;
      &:hover {
        background: $graffit;
      }
    }
  }
  &_main {
    margin-bottom: 0;
    padding: 0;
    .slick-dots {
      top: 0;
      right: 35px;
      left: auto;
      flex-direction: column;
      li {
        padding: 8px 15px;
        width: 100%;
        &.slick-active {
          button {
            background: transparent;
            width: auto;
            height: auto;
            color: $black;
          }
        }
      }
      button {
        color: $light-grey;
        background: transparent;
        width: auto;
        height: auto;
        font-size: 18px;
        &:hover {
          color: $black;
          background: transparent;
        }
      }
    }
  }
  &_extra {
    max-width: 1058px;
    margin-left: auto;
    margin-right: auto;
    .slick-track {
      display: flex;
    }
  }
  &_product {
    max-width: 1145px;
    margin-left: auto;
    margin-right: auto;
    .slick-track {
      display: flex;
    }
  }
  @media screen and (max-width: 1000px){
    &_main {
      .slick-dots {
        top: auto;
        right: 0;
        left: 0;
        bottom: 27px;
        flex-direction: row;
        li {
          padding: 0px 15px;
          width: auto;
        }
      }
    }
  }
  @media screen and (max-width: 768px){
    padding: 0;
    &_extra {
      margin: 0 -15px;
    }
    &_product {
      margin: 0 -15px;
      padding-left: 15px;
    }
    &_previews {
      margin-bottom: 12px;
      .slick-dots {
        bottom: -12px;
      }
    }
  }
}

.slide {
  &__content {
    padding: 140px 0 20px;
    position: relative;
    z-index: 2;
    width: 100%;
    height: 100vh;
    min-height: 810px;
    position: relative;
    display: flex;
    align-items: center;
  }
  &__bg {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    &_color {
      background-color: $light-pink;
      width: 55%;
    }
  }
  &_main {
    position: relative;
    .content {
      width: 100%;
    }
    .slide-card__title,
    .slide-card__text,
    .slide-card__button,
    .card {
      transition: $transitionSlow;
      transform: translateY(100px);
      opacity: 0;
    }
    &.slick-current {
     .slide-card__title,
     .slide-card__text,
     .slide-card__button,
     .card {
        transform: translateY(0);
        opacity: 1;
      }
      .slide-card__title {
        transition-delay: 0.5s;
      }
      .slide-card__text {
        transition-delay: 0.8s;
      }
      .slide-card__button {
        transition-delay: 1.1s;
      }
      .card_medium {
        transition-delay: 0.8s;
      }
      .card_large {
        transition-delay: 1s;
      }
      .card_small {
        transition-delay: 1.2s;
      }
    }
  }
  &_extra {
    height: auto;
    @media screen and (min-width: 769px){
      .product__bg,
      .product__number img,
      .product__name,
      .product__title,
      .product__actions,
      .product__price,
      .product__availability,
      .product__button,
      .product__note {
        transition: $transitionSlow;
        transform: translateY(100px);
        opacity: 0;
      }
      .product__image img {
        transition: opacity 0.6s ease-out 0 transform 0.6s ease-out 0;
        opacity: 0;
      }
      &.slick-current {
        .product__number img,
        .product__name,
        .product__title,
        .product__actions,
        .product__price,
        .product__availability,
        .product__button,
        .product__note {
          transform: translateY(0);
          opacity: 1;
        }
        .product__bg {
          transition-delay: 0.5s;
          transform: translateY(-50%);
          opacity: 1;
        }
        .product__number img {
          transition-delay: 0.8s;
        }
        .product__image img {
          transition: opacity 0.6s ease-out 0.7s transform 0.6s ease-out 0;
          opacity: 1;
        }
        .product__name {
          transition-delay: 0.9s;
        }
        .product__title {
          transition-delay: 1.2s;
        }
        .product__actions,
        .product__price,
        .product__availability,
        .product__button,
        .product__note {
          transition-delay: 1.5s;
        }
      }
    }
  }
  &_previews {
    padding: 0 6px;
  }
  &_product {
    height: auto;
  }
  @media screen and (max-width: 1000px){
    &__content {
      padding: 100px 0 40px;
    }
  }
  @media screen and (max-width: 768px){
    &__content {
      min-height: 650px;
    }
    &_extra {
      padding: 0 15px;
    }
    &_previews {
      padding: 0;
    }
    &_product {
      max-width: 250px;
    }
  }
  @media screen and (max-width: 576px){
    &_product {
      max-width: 172px;
    }
  }
}

@keyframes slideDown {
    0% {
        transform: translateY(0);
    }
    35%{
        transform: translateY(7px);
    }
    60%{
        transform: translateY(-3px);
    }
    95%{
        transform: translateY(2px);
    }
}
