.tabs {
  &-menu {
    display: inline-flex;
    margin: 0 -15px 20px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 15px;
      right: 15px;
      bottom: 0;
      height: 1px;
      background: $light-gray;
    }
    &__item {
      padding: 0 15px;
    }
    &__link {
      display: flex;
      align-items: center;
      height: 100%;
      padding: 0 0 8px;
      cursor: pointer;
      transition: $transition;
      position: relative;
      font-family: $font-text500;
      font-size: 16px;
      line-height: 1.5;
      color: $gray;
      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        height: 2px;
        background: $dark;
        transition: $transition;
        width: 0;
      }
      &.is-active,
      &:hover {
        color: $graffit;
        font-family: $font-text600;
        &::after {
          width: 100%;
        }
      }
    }
  }
  &__note {
    font-family: $font-text300;
    font-size: 14px;
    line-height: 1.71;
    color: $gray;
  }
  @media screen and (max-width: 768px) {
    &-menu {
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      &::after {
        left: 0;
        right: 0;
      }
      &__item {
        padding: 0 5px;
      }
      &__link {
        padding: 0 0 4px;
      }
    }
  }
  @media screen and (max-width: 374px) {
    &-menu {
      &__link {
        text-align: center;
      }
    }
  }
}