.section {
  padding-bottom: 120px;
  &_pt50 {
    padding-top: 53px;
  }
  &_pb60 {
    padding-bottom: 60px;
  }
  &_pb50 {
    padding-bottom: 50px;
  }
  &__title {
    text-align: center;
    margin-bottom: 52px;
    h3 {
      text-transform: uppercase;
    }
    &_left {
      text-align: left;
    }
  }
  @media screen and (max-width: 768px) {
    padding-bottom: 60px;
    &__title {
      margin-bottom: 25px;
    }
  }
}