.menu-catalog {
  list-style: none;
  &__item {
    margin-bottom: 8px;
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $dark;
    font-family: $font-text600;
    &:hover,
    &.is-active {
      color: $green;
    }
    &_special {
      color: $blue-active;
      &:hover,
      &.is-active {
        color: $blue-active;
      }
    }
  }
  &__arrow {
    display: block;
    width: 6px;
    min-width: 6px;
    height: 12px; 
  }
  &__text {
    display: block;
    padding-right: 15px;
    line-height: 1.56
  }
  @media screen and (max-width: 768px) {
    max-width: 300px;
  }
}