.wish {
  position: absolute;
  right: 14px;
  top: 14px;
  &__link {
    color: $gray;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
  &__icon {
    width: 20px;
    height: 20px;
    cursor: pointer;
    transition: $transition;
    color: gray;
    &:hover {
      color: $blue-active;
    }
  }
  &__help {
    position: absolute;
    bottom: 100%;
    margin-bottom: 12px;
    background: $white;
    filter: drop-shadow(0px 2px 8px rgba(0, 0, 0, 0.15));
    padding: 6px 15px;
    right: -8px;
    width: 240px;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
    z-index: -1;
    font-size: 14px;
    color: #595959;
    border-radius: 10px;
    &::after {
      top: 100%;
      right: 12px;
      border: solid transparent;
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
      border-color: rgba(255, 255, 255, 0);
      border-top-color: $white;
      border-width: 5px;
    }
  }
  &:hover {
    .wish__help {
      opacity: 1;
      visibility: visible;
      z-index: 3;
    }
  }
  @media screen and (max-width: 768px) {

  }
}