.product {
  color: $dark;
  position: relative;
  &__view {
    display: block;
    position: relative;
    border-radius: 22px;
  }
  &__image {
    display: block;
    img {
      transition: $transitionSlow;
      max-height: 100%;
      width: auto;
      display: block;
      transform-origin: 0 100%;
    }
  }
  &__bg {
    display: block;
    position: absolute;
    z-index: 1;
    top: 50%;
    &_mobile {
      display: none;
    }
  }
  &__number {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 2;
    img {
      display: block;
    }
  }
  &__name {
    display: block;
    font-family: $font-text600;
    transition: $transition;
    font-size: 26px;
    text-transform: uppercase;
    margin-bottom: 15px;
  }
  &__title {
    display: block;
    transition: $transition;
    margin-bottom: 10px;
    font-size: 20px;
  }
  &__actions {
    margin-bottom: 10px;
    .grid-col {
      padding-bottom: 10px;
    }
  }
  &__availability {
    font-size: 16px;
    font-family: $font-text500;
    color: $blue-light;
  }
  &__wrapper {
    background: $white;
    transition: $transition;
    border-radius: 14px;
    padding: 14px 25px 40px;
    height: 100%;
  }
  &__top {
    position: relative;
    text-align: center;
    padding-bottom: 20px;
    margin-bottom: 6px;
  }
  .price {
    font-size: 24px;
    &_old {
      font-size: 16px;
    }
  }
  &__note {
    font-size: 18px;
    color: $gray;
    margin-bottom: 10px;
  }
  &-previews {
    padding-bottom: 20px;
    &__item {
      height: 64px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid $light-gray;
      border-radius: 14px;
      position: relative;
      img {
        max-height: 100%;
        width: auto;
        position: relative;
        z-index: 5;
      }
    }
  }
  &__loupe {
    position: absolute;
    right: 12px;
    bottom: 12px;
    width: 20px;
    height: 20px;
    color: $gray;
  }
  &__code {
    font-size: 14px;
    line-height: 1.7;
    color: $gray;
    margin-bottom: 14px;
  }
  &__selection {
    margin-bottom: 16px;
    .grid-col {
      padding-bottom: 10px;
    }
  }
  &__describe {
    margin-bottom: 32px;
  }
  &-property {
    display: flex;
    align-items: center;
    &__icon {
      width: 18px;
      min-width: 18px;
      height: 15px;
      margin-right: 12px;
    }
    &:hover {
      color: $blue-light;
    }
  }
  &-line {
    padding-bottom: 15px;
  }
  &-info {
    font-size: 16px;
    color: $gray;
    &__link {
      font-family: $font-text600;
      color: $blue-active;
    }
  }
  &__bottom {
    .grid-col {
      padding-bottom: 10px;
    }
  }
  &:hover {
    .product__image img {
      transform: scale(1.05);
    }
  }
  @media screen and (max-width: 768px) {
    &__number {
      width: 130px;
    }
    &__bg {
      &_mobile {
        display: block;
      }
    }
    &__name {
      font-size: 24px;
      margin-bottom: 6px;
    }
    &__title {
      font-size: 18px;
    }
    &__actions {
      margin-bottom: 13px;
    }
    &__wrapper {
      padding: 14px 15px 40px;
    }
    &__top {
      position: relative;
      text-align: center;
      padding-bottom: 10px;
      margin-bottom: 5px;
    }
    .price {
      font-size: 18px;
    }
    &__visual {
      width: 100%;
      min-width: 100%;
    }
    &-previews {
      &__item {
        height: 100%;
        border: none;
      }
    }
    &__describe {
      display: none;
    }
    &__selection {
      margin-bottom: 2px;
    }
    &-info {
      font-size: 14px;
    }
  }
  @media screen and (max-width: 576px){
    &-layout {
      &__title {
        font-size: 18px;
        margin-bottom: 20px;
      }
    }
  }
}