.menu-icon {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0 -16px;
  color: $dark;
  list-style: none;
  &__item {
    padding: 0 16px;
  }
  &__link {
    width: 32px;
    min-width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: $transition;
    cursor: pointer;
    position: relative;
    &:hover {
      color: $blue-active;
    }
  }
  &__number {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-width: 16px;
    padding: 0 3px;
    height: 16px;
    color: $white;
    background: $red;
    border-radius: 50%;
    top: 0;
    right: 0;
    font-weight: 500;
    font-size: 12px;
    font-family: $font-text500;
    transform: translateX(50%);
  }
  &__image {
    display: block;
    &_heart {
      width: 26px;
      min-width: 26px;
      height: 23px;
    }
    &_search {
      width: 23px;
      min-width: 23px;
      height: 24px;
    }
    &_basket {
      width: 26px;
      min-width: 26px;
      height: 26px;
    }
    &_avatar {
      width: 21px;
      min-width: 21px;
      height: 21px;
    }
  }
  @media screen and (max-width: 576px) {
    margin: 0 -10px;
    &__item {
      padding: 0 10px;
    }
    &__link {
      width: auto;
      min-width: 24px;
      height: auto;
    }
  }
  @media screen and (max-width: 374px) {
    margin: 0 -6px;
    &__item {
      padding: 0 6px;
    }
  }
}