.footer {
  padding: 52px 0 18px;
  background-color: $graffit;
  color: $white;
  h5 {
    margin-bottom: 32px;
  }
  &__contacts {
    font-size: 24px;
    font-family: $font-text500;
    .contacts__item:not(:last-child) {
      margin-bottom: 19px;
    }
  }
  &__info {
    font-family: $font-text500;
    max-width: 200px;
    p {
      color: $white;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__bottom {
    max-width: 575px;
    margin-top: -47px;
  }
  &__copy {
    padding-right: 10px;
  }
  &-line {
    padding-top: 14px;
    font-family: $font-text300;
    font-size: 14px;
    color: $grey;
    &__deliver {
      padding: 0 5px;
    }
    &__link {
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }
  @media screen and (max-width: 1000px) {
    &__bottom {
      margin-top: 20px;
    }
  }
  @media screen and (max-width: 768px) {
    h5 {
      margin-bottom: 16px;
    }
    &__contacts {
      font-size: 18px;
      font-family: $font-text500;
      .contacts__item:not(:last-child) {
        margin-bottom: 8px;
      }
    }
    &-line {
      font-size: 12px;
      &__deliver {
        padding: 0 2px;
      }
    }
    .grid-col_mobile50 {
      order: -1;
    }
    .grid-col_mobile100 {
      &:first-child {
        order: 1;
      }
    }
    &__top {
      .grid-col {
        padding-bottom: 32px;
      }
    }
    &__bottom {
      .grid-col {
        padding-top: 8px;
        padding-bottom: 8px;
      }
    }
  }
}