.basket {
  padding-bottom: 40px;
  color: $dark;
  &__item {
    margin-bottom: 60px;
  }
  &-list {
    h4 {
      margin-bottom: 40px;
    }
    &__item {
      &:not(:last-child) {
        margin-bottom: 40px;
      }
    }
    &__right {
      width: 185px;
      min-width: 185px;
      text-align: right;
    }
    &__row {
      &:not(:last-child) {
        margin-bottom: 28px;
      }
    }
    &__delete {
      display: block;
      width: 15px;
      height: 19px;
      cursor: pointer;
      transition: $transition;
      &:hover {
        color: $blue-active;
      }
    }
    &__details {
      font-family: $font-text500;
      font-size: 16px;
      color: $gray;
    }
  }
  &-present {
    &__title {
      display: flex;
      align-items: center;
      margin-bottom: 44px;
    }
    &__icon {
      width: 52px;
      min-width: 52px;
      margin-right: 24px;
    }
    &__item {
      margin-bottom: 20px;
    }
    &__total {
      border-top: 1px solid $gray;
      padding-top: 40px;
      margin-top: 40px;
    }
    &__selected {
      font-size: 20px;
      max-width: 400px;
    }
    .price {
      font-size: 26px;
    }
  }
  &__right {
    width: 364px;
    min-width: 364px;
  }

  &-aside {
    background: $white;
    border: 1px solid $grey-border;
    border-radius: 14px;
    padding: 31px 24px;
    &__link {
      color: $blue-active;
      font-size: 16px;
      &:hover {
        color: $dark;
      }
    }
    &__top {
      margin-bottom: 32px;
    }
    &__item {
      &:not(:last-child) {
        margin-bottom: 24px;
      }
      &_total {
        padding-top: 24px;
        border-top: 1px solid rgba(107, 107, 107, 0.1);
      }
    }
    &__line {
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }
    &__name {
      font-family: $font-text500;
      font-size: 16px;
    }
    &__data {
      min-width: 100px;
      .price_new {
        margin-right: 0;
      }
      &_total {
        .price {
          font-size: 26px;
        }
      }
    }
    &__note {
      font-family: $font-text500;
      font-size: 14px;
      color: $gray;
    }
    &__button {
      .button {
        text-transform: none;
        width: 100%;
      }
    }
  }
  @media screen and (max-width: 1000px) and (min-width: 769px) {
    &-list {
      &__right {
        width: 100%;
        min-width: 100%;
        text-align: left;
      }
      &__row {
        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }
  }

  @media screen and (max-width: 768px) {
    &-list {
      h4 {
        margin-bottom: 30px;
      }
      &__item {
        &:not(:last-child) {
          border-bottom: 1px solid #B8B8B8;
          margin-bottom: 30px;
          padding-bottom: 30px;
        }
      }
      &__row {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
      &__details {
        font-size: 14px;
      }
    }
    &__right {
      width: 100%;
      min-width: 100%;
    }
    &-aside {
      max-width: 334px;
      margin: 0 auto;
    }
    &-present {
      &__title {
        margin-bottom: 28px;
      }
      &__icon {
        width: 32px;
        min-width: 32px;
      }
      &__total {
        padding-top: 20px;
        margin-top: 20px;
      }
      &__selected {
        font-size: 16px;
      }
    }
    &__item {
      margin-bottom: 35px;
    }
  }
  @media screen and (max-width: 576px) {
    &-list {
      &__right {
        width: 100%;
        min-width: 100%;
        text-align: left;
      }
      &__row {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
      &__price {
        text-align: left;
      }
    }
    &-aside {
      padding: 20px 15px;
    }
  }
}