.list {
  font-size: 14px;
  line-height: 1.71;
  font-family: $font-text500;
  list-style: none;
  color: $gray;
  &:not(:last-child) {
    margin-bottom: 16px;
  }
  li {
    position: relative;
    padding-left: 16px;
    &::before {
      width: 6px;
      height: 6px;
      background: $blue-light;
      border-radius: 50%;
      position: absolute;
      left: 0;
      top: 9px;
      content: '';
    }
  }
  @media screen and (max-width: 1000px){

  }
}