.product_card {
  padding-bottom: 60px;
  .product__visual {
    width: 370px;
    min-width: 370px;
  }
  .product__image {
    height: 359px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      position: relative;
      z-index: 3;
    }
  }
  .product__bg {
    border-radius: 14px;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 233px;
    height: 248px;
  }
  .product__name {
    padding-top: 7px;
  }
  .product__actions {
    margin-bottom: 16px;
  }
  .price {
    font-size: 34px;
  }
  .product__availability {
    margin-bottom: 14px;
  }
  .label-block {
    padding-top: 0;
    top: -12px;
    left: -20px;
  }
  .product__button {
    margin-bottom: 48px;
  }
  .product__view:hover {
    .product__image img {
      transform: scale(1.05);
    }
  }

  &:hover {
    .product__image img {
      transform: none;
    }
  }

  @media screen and (max-width: 768px) {
    .product__view {
      display: none;
    }
    .product__name {
      font-size: 20px;
      margin-bottom: 6px;
      padding-top: 0;
    }
    .product__code {
      margin-bottom: 4px;
    }
    .product__actions {
      margin-bottom: 5px;
    }
    .product__price {
      margin-bottom: 8px;
    }
    .price {
      font-size: 32px;
    }
    .product__availability {
      margin-bottom: 27px;
    }
    .product__button {
      margin-bottom: 0;
    }
  }

  @media screen and (max-width: 576px) {
    .product__button {
      .button {
        width: 100%;
      }
    }
    .product__visual {
      width: 100%;
      min-width: 100%;
    }
  }
}