.header {
  padding: 53px 0 49px;
  position: fixed;
  z-index: 15;
  background: transparent;
  top: 0;
  left: 0;
  right: 0;
  &::before {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: linear-gradient(180deg, #FFF6F7 55.5%, rgba(255, 246, 247, 0) 97.04%);
    transition: $transition;
    opacity: 0;
    content: '';
    z-index: -1;
  }
  &__menu-icon {
    min-width: 36%;
  }
  &-search {
    width: 32px;
    overflow: hidden;
    transition: $transition;
    &__wrapper {
      position: relative;
      padding-left: 32px;
      transition: $transition;
    }
    .menu-icon__link {
      position: absolute;
      top: 0;
      left: 0;
    }
    .search {
      width: 200px;
    }
    &.is-open {
      width: 200px;
      .header-search__wrapper {
        transform: translateX(-32px);
      }
    }
  }
  &.is-up {
    &::before {
      opacity: 1;
    }
  }

  @media screen and (max-width: 1280px) {
    &__menu-icon {
      min-width: 0;
    }
    .content {
      position: relative;
    }
    &-search {
      width: auto;
      overflow: visible;
      &__wrapper {
        position: static;
        padding-left: 0;
      }
      .menu-icon__link {
        position: static;
      }
      .search {
        width: 100%;
        padding: 15px;
        background: $white;
        box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        margin-top: 20px;
        transition: $transition;
        opacity: 0;
        transform: translateY(-100%);
        z-index: -1;
      }
      &.is-open {
        width: auto;
        .header-search__wrapper {
          transform: none;
        }
        .search {
          opacity: 1;
          transform: translateY(0);
          z-index: 2;
        }
      }
    }
  }
  @media screen and (max-width: 1000px) {
    padding: 23px 0;
    background: $light-pink;
    &::before {
      display: none;
    }
    &__menu {
      display: none;
    }
  }
  @media screen and (max-width: 374px) {
    .grid-col {
      padding: 0 10px;
    }
    .grid-row {
      margin: 0 -10px;
    }
  }
}