.card {
  display: inline-flex;
  position: relative;
  color: $dark;
  &__bg {
    border-radius: 14px;
    padding: 23px 95px 30px 25px;
    width: 100%;
    display: block;
  }
  &__title {
    font-family: $font-text500;
    font-size: 26px;
    text-transform: uppercase;
    margin-bottom: 10px;
    display: block;
    position: relative;
    z-index: 3;
  }
  &__describe {
    display: block;
    font-size: 20px;
    line-height: 1.4;
    position: relative;
    z-index: 3;
  }
  &__image {
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    z-index: 1;
    overflow: hidden;
    width: 90%;
    img {
      transition: $transitionSlow;
      max-height: 100%;
      width: auto;
      display: block;
      transform-origin: 0 100%;
    }
  }
  &__price {
    display: block;
    padding-top: 20px;
    font-size: 26px;
    position: relative;
    z-index: 3;
  }
  &__note {
    font-family: $font-text500;
    font-size: 14px;
    position: relative;
    z-index: 3;
    color: $gray;
    max-width: 200px;
    display: block;
  }
  &_medium {
    padding: 40px 13px 75px 0;
    .card__bg {
      width: 284px;
      height: 307px;
    }
  }
  &_large {
    padding: 0 55px 74px 0;
    .card__bg {
      width: 369px;
      height: 270px;
    }
    .card__image {
      bottom: -27px;
    }
  }
  &_small {
    padding: 0 81px 28px 0;
    .card__bg {
      width: 253px;
      height: 268px;
    }
  }
  &:hover {
    .card__image img {
      transform: scale(1.05);
    }
  }
  @media screen and (max-width: 1000px) {
    &_medium {
      padding-top: 0;
    }
  }
  @media screen and (max-width: 768px) {
    padding-right: 33px;
    &__bg {
      padding: 15px 60px 30px 15px;
    }
    &__title {
      font-size: 18px;
      margin-bottom: 3px;
    }
    &__describe {
      font-size: 16px;
    }
    &__note {
      font-size: 12px;
    }
    &_medium {
      .card__bg {
        width: 192px;
        height: 207px;
      }
      .card__image {
        width: 60%;
      }
    }
    &_large {
      padding-right: 0;
      .card__bg {
        width: 246px;
        height: 182px;
      }
    }
    &_small {
      .card__image {
        width: 70%;
      }
      .card__bg {
        width: 176px;
        height: 190px;
      }
    }
  }
}