.menu-sub {
  list-style: none;
  font-size: 14px;
  line-height: 1.71;
  padding-left: 20px;
  transition: $transition;
  &__item {
    margin-bottom: 4px;
  }
  &__link {
    color: $gray;
    font-family: $font-text500;
    position: relative;
    display: inline-block;
    &::after {
      content: '';
      width: 6px;
      height: 6px;
      background: $blue-light;
      border-radius: 50%;
      position: absolute;
      top: 9px;
      left: 0;
      opacity: 0;
      transition: $transition;
    }
    &:hover,
    &.is-active {
      padding-left: 15px;
      font-family: $font-text600;
      &::after {
        opacity: 1;
      }
    }
  }
  @media screen and (max-width: 1000px) {
    padding: 15px;
    &__item {
      margin-bottom: 4px;
    }
  }
}