.wrap-all {
  display: flex;
  width: 100%;
  flex-direction: column;
  min-height: 100vh;
}
.wrap-content {
  flex-grow: 1;
  width: 100%;
  overflow: hidden;
}
.grid-row {
  display: flex;
  margin: 0 -15px;
  &_between {
    justify-content: space-between;
  }
  &_middle {
    align-items: center;
  }
  &_bottom {
    align-items: flex-end;
  }
  &_baseline {
    align-items: baseline;
  }
  &_center {
    justify-content: center;
  }
  &_wrap {
    flex-wrap: wrap;
  }
  &_reverse {
    flex-direction: row-reverse;
  }
  &_clean {
    margin: 0;
    .grid-col {
      padding: 0;
    }
  }
  @media screen and (max-width: 1000px) {
    &_tabletcolumn {
      flex-direction: column;
    }
    &_tabletblock {
      display: block;
    }
  }
  @media screen and (max-width: 768px) {
    &_mobileblock {
      display: block;
    }
    &_mobilecolumn {
     flex-direction: column;
    }
    &_mobileflex {
      display: flex;
    }
    &_mobilewrap {
      flex-wrap: wrap;
    }
    &_mobilebetween {
      justify-content: space-between;
    }
  }
  @media screen and (max-width: 576px) {
    &_minblock {
      display: block;
    }
  }
}
.grid-col {
  padding: 0 15px;
  &_25 {
    width: 25%;
    min-width: 25%;
  }
  &_33 {
    width: 33.3333%;
    min-width: 33.3333%;
  }
  &_40 {
    width: 40%;
    min-width: 40%;
  }
  &_50 {
    width: 50%;
    min-width: 50%;
  }
  &_60 {
    width: 60%;
    min-width: 60%;
  }
  &_66 {
    width: 66.6667%;
    min-width: 66.6667%;
  }
  &_75 {
    width: 75%;
    min-width: 75%;
  }
  &_100 {
    width: 100%;
    min-width: 100%;
  }
  &_full {
    flex-grow: 1;
    min-width: 0;
  }
  @media screen and (max-width: 1280px) {
    &_desktop70 {
      width: 70%;
      min-width: 70%;
    }
    &_desktop30 {
      width: 30%;
      min-width: 30%;
    }
    &_desktop33 {
      width: 33.3333%;
      min-width: 33.3333%;
    }
  }
  @media screen and (max-width: 1000px) {
    &_tablet100 {
      width: 100%;
      min-width: 100%;
    }
    &_tablet25 {
      width: 25%;
      min-width: 25%;
    }
  }
  @media screen and (max-width: 768px) {
    &_mobile100 {
      width: 100%;
      min-width: 100%;
    }
    &_mobile50 {
      width: 50%;
      min-width: 50%;
    }
    &_mobile33 {
      width: 33.3333%;
      min-width: 33.3333%;
    }
  }
  @media screen and (max-width: 576px) {
    &_min100 {
      width: 100%;
      min-width: 100%;
    }
    &_min50 {
      width: 50%;
      min-width: 50%;
    }
  }
}
.grid-wrap {
  display: flex;
  flex-direction: column;
  height: 100%;
  &__top {
    flex: 1 0 auto;
    display: block;
  }
}