.page {
  display: flex;
  width: 100%;
  &__aside {
    width: 220px;
    min-width: 220px;
    padding: 176px 25px 50px 0;
    position: relative;
    &::after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      background: $light-pink;
      width: 50vw;
      z-index: -1;
      content: '';
    }
  }
  &__main {
    flex-grow: 1;
    padding: 131px 0 20px 48px;
    min-width: 0;
    width: calc(100% - 220px);
    .product-slider {
      margin: 0 -37px;
    }
  }
  &__title {
    margin-bottom: 40px;
  }
  @media screen and (max-width: 1460px){
    &__main {
      .product-slider {
        margin: 0;
      }
    }
  }
  @media screen and (max-width: 1000px){
    display: block;
    &__aside {
      width: 100%;
      min-width: 100%;
      padding: 94px 0 16px;
      text-align: center;
      &::after {
        display: none;
      }
    }
    &__main {
      padding-top: 0;
      padding-left: 0;
      width: 100%;
    }
  }
}
@media screen and (max-width: 768px) {
  &__title {
    font-size: 18px;
    margin-bottom: 30px;
  }
}