.counter {
  &__input {
    height: 26px;
    width: 36px;
    min-width: 36px;
    padding: 0 5px;
    font-size: 18px;
    border: 1px solid #DDDDDD;
    color: $black;
    text-align: center;
  }
  .ui-spinner {
    display: flex;
  }
  .ui-spinner-button {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 26px;
    width: 36px;
    min-width: 36px;
    background: #D0D0D0;
    border-radius: 1px;
    font-size: 18px;
    color: $black;
    cursor: pointer;
    &.ui-spinner-down {
      order: -1;
      &::after {
        content: '-';
      }
    }
    &.ui-spinner-up {
      &::after {
        content: '+';
      }
    }
    &:hover {
      background: $blue-active;
    }
  }
  @media screen and (max-width: 768px) {

  }
}