.radio {
  display: inline-block;
  cursor: pointer;
  font-size: 20px;
  input[type="radio"] {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    left: -99999px;
    &:checked + .radio__wrapper {
      color: $dark;
      .radio__icon {
        border: 1px solid $dark;
        &::after {
          opacity: 1;
        }
      }
    }
  }
  &__wrapper {
    display: flex;
    min-width: 20px;
    margin: 0 -8px;
  }
  &__text {
    padding: 0 8px;
    display: flex;
    min-height: 20px;
    align-items: center;
  }
  &__icon {
    width: 20px;
    min-width: 20px;
    height: 20px;
    transition: $transition;
    border: 1px solid $gray;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 8px;
    &::after {
      opacity: 0;
      transition: $transition;
      width: 12px;
      height: 12px;
      background: $dark;
      display: block;
      content: '';
      border-radius: 50%;
    }
  }
  &_block {
    input[type="radio"] {
      &:checked + .radio__wrapper {
        font-family: $font-text500;
        border-color: $dark;
      }
    }
    .radio__wrapper {
      min-width: 68px;
      height: 42px;
      justify-content: center;
      text-align: center;
      align-items: center;
      font-size: 16px;
      letter-spacing: 0.02em;
      color: $gray;
      border: 1px solid $light-gray;
      transition: $transition;
      margin: 0;
    }
    .radio__text {
      padding: 0 8px;
    }
  }
  &_compose {
    input[type="radio"] {
      &:checked + .radio__wrapper .radio__text {
        border: 2px solid $blue-active;
      }
    }
    .radio__wrapper {
      display: block;
      margin: 0;
      max-width: 265px;
    }
    .radio__icon {
      display: none;
    }
    .radio__text {
      padding: 24px;
      transition: $transition;
      border: 1px solid $grey;
      border-radius: 14px;
      min-height: 170px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .radio__title {
      display: block;
      margin-bottom: 11px;
      font-size: 24px;
      font-family: $font-text500;
      border-color: $dark;
    }
    .radio__conditions {
      display: block;
      margin-bottom: 14px;
      font-size: 16px;
      font-family: $font-text500;
      border-color: $gray;
    }
    .radio__bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: $graffit;
      font-size: 16px;
      font-family: $font-text600;
    }
  }
  @media screen and (max-width: 768px) {
    font-size: 16px;
    &__wrapper {
      min-width: 16px;
      margin: 0 -6px;
    }
    &__text {
      padding: 0 6px;
      min-height: 16px;
    }
    &__icon {
      width: 16px;
      min-width: 16px;
      height: 16px;
      margin: 0 6px;
      &::after {
        width: 8px;
        height: 8px;
      }
    }
    &_compose {
      input[type="radio"] {
        &:checked + .radio__wrapper .radio__text {
          border: none;
        }
      }
      .radio__wrapper {
        display: flex;
        margin: 0 -6px;
        max-width: none;
      }
      .radio__icon {
        display: flex;
      }
      .radio__text {
        padding: 0;
        border: none;
        border-radius: 0;
        min-height: 0;
        display: block;
      }
      .radio__title {
        margin-bottom: 4px;
        font-size: 18px;
      }
      .radio__conditions {
        margin-bottom: 8px;
        font-size: 14px;
      }
      .radio__bottom {
        justify-content: flex-start;
      }
    }
  }
}