.product_full {
  height: 100%;
  .product__image {
    height: 465px;
    border-radius: 22px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 5px 35px;
    img {
      position: relative;
      z-index: 3;
      transition: $transition;
    }
  }
  .product__bg {
    width: 290px;
    height: 288px;
    right: -18px;
    transform: translateY(-50%);
    border-radius: 24px 24px 24px 0px;
  }
  .product__actions {
    margin-bottom: 38px;
  }
  .product__price {
    padding-left: 33px;
  }
  .price {
    font-size: 42px;
  }
  .product__availability {
    margin-bottom: 48px;
    padding-left: 33px;
  }
  .product__title {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 43px;
  }
  @media screen and (max-width: 768px) {
    max-width: 250px;
    .grid-row_mobilecolumn {
      height: 100%;
    }
    .product__info {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
    }
    .product__view {
      max-width: 225px;
      margin: 0 auto 50px;
    }
    .product__image {
      height: 214px;
    }
    .product__name {
      margin-bottom: 8px;
    }
    .product__actions {
      margin-bottom: 5px;
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }
    .product__price {
      padding-left: 0;
    }
    .price {
      font-size: 32px;
    }
    .product__availability {
      margin-bottom: 18px;
      padding-left: 0;
    }
    .product__bg {
      width: 133px;
      height: 133px;
      right: -8px;
    }
    .button {
      width: 100%;
      min-width: 0;
    }
  }
}