.button {
  display: inline-flex;
  cursor: pointer;
  transition: $transition;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  font-size: 16px;
  padding: 0 24px;
  color: $white;
  min-width: 256px;
  text-align: center;
  height: 48px;
  border: none;
  text-transform: uppercase;
  font-family: $font-text500;
  font-weight: 400;
  letter-spacing: 0.02em;
  border-radius: 4px;
  &_primary {
    background: $graffit;
    color: $white;
  }
  &_secondary {
    background: transparent;
    color: $blue-active;
    border: 1px solid $blue-active;
    min-width: 0;
  }
  &_variant {
    background: transparent;
    color: $gray;
    border: 1px solid $gray;
    min-width: 0;
  }
  &:hover {
    border-color: $blue-active;
    background: $blue-active;
    color: $white;
  }
  &_variant {
    &:hover {
      background: transparent;
      color: $blue-active;
      border-color: $blue-active;
    }
  }
  @media screen and (max-width: 374px) {
    padding: 0px 15px;
    font-size: 14px;
    min-width: 0;
    &_variant {
      width: 100%;
    }
  }
}
