body {
  background: $pink;
  color: $black;
  font-family: $font-text;
  line-height: 1.2;
  font-size: 18px;
  @media screen and (max-width: 768px) {
    font-size: 16px;
  }
}
.content {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1360px;
}
.hidden {
  display: none;
}
.mobile-show {
  display: none;
}
.tablet-show {
  display: none;
}
@media screen and (max-width: 768px) {
  .mobile-hidden {
    display: none;
  }
  .mobile-show {
    display: block;
  }
}
@media screen and (max-width: 1000px) {
  .tablet-hidden {
    display: none;
  }
  .tablet-show {
    display: block;
  }
}
.text-right {
  text-align: right;
}
.text-center {
  text-align: center;
}
.svg-icon {
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  color: inherit;
}
