.menu-top {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0 -27px;
  color: $dark;
  list-style: none;
  &__item {
    padding: 0 27px;
  }
  &__link {
    display: block;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -5px;
      height: 1px;
      background: $dark;
      transition: $transition;
      width: 0;
    }
    &.is-active,
    &:hover {
      &::after {
        width: 100%;
      }
    }
  }
  @media screen and (max-width: 1280px) {
    justify-content: center;
    margin: 0 -15px;
    &__item {
      padding: 0 15px;
    }
  }
  @media screen and (max-width: 1000px) {
    justify-content: flex-start;
    margin-bottom: 6px;
    &__item {
      padding-bottom: 10px;
    }
  }
  @media screen and (max-width: 576px) {
    margin: 0 -10px 6px;
    &__item {
      padding: 0 10px 10px;
    }
  }
  @media screen and (max-width: 374px) {
    margin: 0 -5px 6px;
    font-size: 15px;
    &__item {
      padding: 0 5px 10px;
    }
  }
}