.search {
  &__wrapper {
    position: relative;
  }
  &__input {
    padding: 0 14px 0 37px;
  }
  &__submit {
    width: 17px;
    height: 17px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    background: transparent;
    border: none;
    color: inherit;
    transition: $transition;
    cursor: pointer;
    &:hover {
      color: $blue-active;
    }
  }
  &__close {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    transition: $transition;
    display: none;
    color: gray;
    &:hover {
      color: $blue-active;
    }
  }
  @media screen and (max-width: 1280px) {
    &__close {
      display: block;
    }
  }
}