.coupon {
  display: flex;
  position: relative;
  &__input {
    background: $white;
    border: 1px solid $grey-border;
    box-sizing: border-box;
    border-radius: 4px 0px 0px 4px;
    flex-grow: 1;
    min-width: 0;
    height: 48px;
    padding: 0 14px;
  }
  &__button {
    border-radius: 0px 4px 4px 0px;
    width: 102px;
    min-width: 102px;
    background: $gray;
    text-transform: none;
    padding: 0 5px;
    &.is-disabled {
      background: #ECECEC;
      color: #B6B6B6;
    }
  }
  label.error {
    position: absolute;
    top: 100%;
    left: 0;
  }
  &-message {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #F2F2F2;
    border: 1px solid #ECEAE7;
    border-radius: 4px;
    font-family: $font-text500;
    font-size: 16px;
    letter-spacing: 0.02em;
    color: #565656;
    display: none;
    &__wrapper {
      display: flex;
      height: 100%;
      padding: 0 15px;
      align-items: center;
      justify-content: space-between;
    }
    &__icon {
      width: 15px;
      min-width: 15px;
      height: 11px;
      color: $blue-active;
    }
  }
  &_complete {
    .coupon-message {
      display: block;
    }
  }
}